            <h3 class="text-center"><strong>崔华</strong></h3>
            <pre style="font-size:large;" class="text-center">教授  博士生导师</pre>
            <p class="p_content">崔华，中国科学技术大学化学系教授。2000年入选中国科学院“引进海外杰出人才”，
                2006年获国家自然科学基金委“国家杰出青年基金”，2012年获中国化学会“分析化学基础研究梁树权奖”（国内分析化学的最高奖项），2013年获“中国科学院第四届十大杰出妇女”称号，2015年获得中国化学会分析化学专业委员会“中国女分析化学家奖”。2015-2016年任国际生物发光与化学发光理事会成员。另外，目前还担任《Anal.
                Bioanal. Chem.》Editor以及《ACS
                sensor》和《分析化学》等杂志编委。主要从事纳米化学发光及其在生物分析中的应用研究。近年来，作为项目负责人先后承担了国家自然基金委国家重大科研仪器研制项目“研究单纳米粒子催化反应的化学发光与光热多功能成像系统”和科技部国家重点研发计划纳米科技专项项目“无标记纳米化学发光新一代体外诊断技术及其在急性心肌梗死快速诊断中的应用”，取得一系列创新性成果。开拓了金属纳米化学发光与电化学发光新领域；发明了发光试剂功能化纳米材料的“直接合成法”，率先合成化学发光试剂/催化剂双功能化新型纳米材料，发现其具有优异的异相化学发光催化特性，发展了高灵敏度的化学发光分析新方法，并将其用于重大疾病相关分析物的检测。相关工作受到国际同行的重要关注，受邀在法国、瑞典召开的16th
                & 18th International Symposium on Bioluminescence & Chemiluminescence和在法国召开的2nd International
                Meeting on
                Electrogenerated Chemiluminescence作大会特邀报告。</p>

            <h4><strong>• 研究方向</strong></h4>
            <p class="p_content">主要从事纳米化学发光与电致化学发光及其在免疫分析和DNA分析中的应用研究。</p>

            <h4><strong>• 教学实践</strong></h4>
            <p class="p_content">目前主讲一门研究生课程，并与其他教授合讲两门本科生课程以及一门研究生课程：</p>
            <li style="list-style: none; font-weight: lighter;">
                <ul>仪器分析（本科生课程）</ul>
                <ul>高等分析方法（本科生课程）</ul>
                <ul>分子光谱分析进展（研究生课程）</ul>
                <ul>当代分析科学（研究生课程）</ul>
            </li>

            <h4><strong>• 主要学术成绩、创新点及其科学意义 </strong></h4>
            <p class="p_content">
                发现电致化学发光的多通道发射现象及其对电极电位、电极材料和电极表面状态等的依赖性，开辟了电位分辨的电致化学发光这一新的研究领域，推动了电致化学发光的深入研究；率先将纳米修饰电极用于电致化学发光的研究，发现鲁米诺等传统电致化学发光体系在纳米修饰电极上具有优异的电致化学发光特性；发现金属纳米粒子可作为能量接受体、微尺度反应平台、还原剂、催化剂等参与液相化学发光反应，将液相化学发光反应的研究对象从分子、离子体系扩展到金属纳米体系，开发了一系列新的化学发光体系；率先开展了化学发光功能化的金属纳米材料的合成、化学发光特性及其在生物分析中的应用研究，发展了一系列新的、简单的一步合成法，将发光试剂在合成的过程中直接键合到纳米金属的表面，成功制备了鲁米诺等多种发光试剂功能化的金和银纳米新材料，发现其具有良好的化学发光活性，实现了发光分子的富集和信号的放大，在此基础上进一步构建了高灵敏的生物分析探针，发展了一系列基于化学发光功能化纳米探针的免疫分析和DNA分析新方法。这些研究开创了化学发光与电致化学发光新的研究领域，不仅对化学发光、电致化学发光和纳米科学的基础理论研究具有重要的意义，而且在分析化学、生命科学、环境科学领域具有广阔的应用前景。到目前为止，她所领导的课题组已在Anal.
                Chem.、Chem. Eur. J、 J. Phys. Chem. B & C、 Chem. Comm.、 Biosens.
                Bioelectron.、Analyst等国际重要化学期刊上发表SCI论文100多篇，受到同行专家的高度关注。</p>

