                <!-- 第四段 -->
                <ul><strong>四、化学发光新体系的探索</strong></ul>
                <p class="text-justify p_content"> 尽管人们已发现许多化学反应可产生化学发光，
                    但大部分化学反应的发光量子产率较低，可用于化学发光分析的化学反应却较少，由此限制了化学发光分析的应用范围。因此，探索新的化学发光反应是目前化学发光分析的重要研究课题之一。我们发现了Ce(IV)+Tween20、Ce(IV)+罗丹明6G+酚类化合物化学发光新体系，提出了其反应机理，建立了多种酚类和黄酮类化合物的高灵敏度化学发光分析新方法。
                </p>
                <div style="margin: 20px auto;">
