                    20体系的首篇论文于2001年发表在Analyst上，2001年5月该篇论文在Analyst上被点击次数进入前10名，排名第三。</p>
                <!-- 第五段 -->
                <ul><strong>五、化学发光抑制和增强作用</strong></ul>
                <p class="p_content">化学发光和电致化学发光分析可根据参与物与化学发光强度的线性关系对参与物进行测定。
                    但化学发光与电致化学发光的体系较少，可探测的物种非常有限。对不参与化学反应的物质的测定常根据其对化学发光或电致化学发光反应的抑制和增强作用进行分析。但抑制和增强作用的规律和机理尚不清楚，抑制剂和增强剂的发现常常需要通过反复试验进行摸索。
                </p>
                <p class="p_content">
                    我们率先系统探索了一系列酚类和苯胺类化合物以及氨基酸在各种条件下对鲁米诺-铁氰化钾、鲁米诺-H2O2-Co(II)、鲁米诺-DMSO-OH、鲁米诺-H2O2-IO4-、光泽精-H2O2-Co(II)、Ce(IV)-Tween20等化学发光体系和鲁米诺、Ru(bpy)32+/C2O42-,
                    Ru(bpy)32+/TPA电致化学发光体系的影响，发现一系列酚类和苯胺类化合物以及氨基酸的化学发光与电致化学发光的抑制和增强作用规律及其与分子结构、化合物的浓度、溶液pH的相关性，提出了其化学发光与电致化学发光的抑制和增强作用机理，建立了一系列生物和环境重要的酚类化合物、苯胺类化合物以及氨基酸的流动注射化学发光抑制和增强分析法和同时测定多种化合物的高效液相色谱－化学发光抑制和增强检测法。此项工作对于深入认识化学发光抑制和增强机理具有重要的理论意义，对新的增强剂和抑制剂的发现具有重要的指导意义。与此同时，扩展了化学发光与电致化学发光分析的应用范围，在环境监测、食品分析、药物分析、烟草分析等领域具有重要的应用价值。
                </p>
