var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('div',{staticClass:"mainLayout"},[_c('el-card',{staticClass:"content-size"},[_c('h1',{staticClass:"text-center"},[_vm._v("基本信息")]),_c('el-divider'),_c('el-carousel',{attrs:{"interval":3000,"type":"card","height":"327px"}},_vm._l((_vm.imgs),function(item){return _c('el-carousel-item',{key:item},[_c('el-tooltip',{attrs:{"content":item.title,"placement":"top"}},[_c('el-image',{staticClass:"img-size",attrs:{"src":item.src,"preview-src-list":item.src}})],1)],1)}),1),_c('div',[_c('p',{staticClass:"p_content",staticStyle:{"margin-top":"20px"}},[_vm._v(" 实验室现位于中国科技大学化学楼311、314、209和208，总面积115平方米。目前的研究团队共有13人，包括由崔华 教授指导的四名在读博士研究生、四名硕士研究生（包括三名硕博连读生）和四名本科生，已经形成一支包括各年级，多层次 研究人员的科研队伍。 ")]),_c('p',{staticClass:"p_content"},[_vm._v(" 在过去的七年里，实验室先后培养了十一名博士研究生（其中两人与林祥钦教授合作指导）（孙玉刚、李凤、周建、 Aung Myint、张志锋、张群林、于宏晓、万国晖、石明娟、郭吉兆、董永平）、 八名硕士毕业生（李世凤、谢成根、邹桂征、 赵晓宇、徐杨、庞永强、王成名、徐胜良）以及十一名本科生（许峰、程佳懿、赖春泽、廉玫、 盼怡兰、高仲栋、吴云龙、周伟、 石云波、熊涛、刘丽佳），并接受高级访问学者一名。 ")]),_c('p',{staticClass:"p_content"},[_vm._v(" 实验室现有的仪器设备包括：电致化学发光系统五套、多维光谱电化学系统一套、CCD光谱/显微成像系统、 高效液相色谱－化学发光检测系统二套、流动注射化学发光仪四台。 近两年来先后购进荧光光谱仪（Hitachi,F-7000）、 紫外光谱仪（Agilent,UV-8453）、微孔板化学发光仪以及超纯水系统、高速离心机等配套仪器。借助中国科技大学理化实验中 心和化学院实验中心平台，本课题组还可方便进行各种物理化学测试和表征实验。 ")]),_c('p',{staticClass:"p_content"},[_vm._v(" 实验室先后承担中国科学院“引进海外杰出人才”基金（“百人计划”，2002）、 国家自然科学基金杰出青年基金（2007）、国家自然科学基金 面上项目（1999，2004，2006）三项等纵向科研 项目，累计研究经费近500万元。已结题项目均受到评审专家的好评。")]),_c('p',{staticClass:"p_content"},[_vm._v(" 在过去的研究工作中，我们发现电致化学发光的多通道发射现象及其对电极电位、电极材料和电极表面状态等的依 赖性，开辟了电位分辨的电致化学发光这一新的研究领域。率先将纳米修饰电极用于电致化学发光的研究，发现了鲁米诺等 传统ECL体系在纳米修饰电极上具有优异的ECL特性。发现纳米金可作为能量接受体、微尺度反应平台、还原剂、催化剂等参与液相 化学发光反应, 将液相化学发光反应的研究对象从分子、离子体系扩展到金属纳米体系，进一步丰富了人们对微尺度结构材料特 殊性质的认识。发现了化学发光与电致化学发光的增强和抑制作用的规律及其与pH、分子结构、溶液浓度等的相关性，发现了C e (IV) - 吐温20等化学发光新体系，据此建立了多种酚类和黄酮类化合物的化学发光分析新方法。")]),_c('p',{staticClass:"text-justify p_content"},[_vm._v(" 本课题组目前的研究方向是化学发光与电化学、色谱、纳米科学的交叉研究及其分析应用。研究课题主要包括：")]),_c('li',{staticClass:"text-left 