<template>
    <div>
        <nav-menu></nav-menu>
        <el-card class="loginLayout flexLayout">
            <div>
                <el-input placeholder='用户名' v-model='username' clearable class="inputStyle" prefix-icon="el-icon-user">
                </el-input>
            </div>
            <div>
                <el-input placeholder='密码' v-model='password' show-password class="inputStyle"
                    prefix-icon="el-icon-edit-outline"></el-input>
            </div>
            <div class="flexLayout" style="flex-direction: row;">
                <el-button type='primary' plain @click = 'login'>登录</el-button>
                <el-link type='primary' :underline='false'>忘记密码？</el-link>
            </div>

        </el-card>
    </div>
</template>
<script>
    import NavMenu from '../components/NavMenu.vue'
    export default {
        name: 'login',
        data() {
            return {
                username: '',
                password: '',
            }
        },
        methods:{
            login: function(){
                if(this.username ==='admin'&& this.password ==='123456'){
                    this.$message({
                        message:'登陆成功',
                        type:'success'
                    })
                    console.log('success')
                    this.$router.replace('/AdminPage')
                }else if(this.username === ''||this.password ===''){
                    this.$message({
                        message:'请输入账号或密码',
                        type:'warning'
                    })
                }
                else{
                    this.$message({
                        message:'账号或密码输入错误',
                        type:'error'
                    })
                }
            }
        },
        components:{
            NavMenu,
        }
    }
</script>
<style scoped>
    .loginLayout {
        min-height: 300px;
        width: 600px;
        margin: 200px auto;
    }

    .flexLayout {
        display: -webkit-flex;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .inputStyle {
        width: 200px;
        margin-bottom: 30px;
    }
</style>